@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');



.landing-form {
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'Montserrat', sans-serif;
    background: linear-gradient(to left, #8942a8, #ba382f);
    height: 100vh;
    width: 100vw;
    z-index: 99;
}

.landing-title {
    color: white;
    font-weight: 500;
    font-size: 4rem;
    display: block;
    padding-top: 15rem;
}

.landing-form p {
    color: white;
    font-size: 1.5rem;
}

.btn-animation {
    padding: .75rem;
    border: 1px solid white;
    margin-right: 5px;
    margin-left: 5px;
    text-align: center;
    color: white;
    transition: .5s ease;
}

.btn-animation:hover {
    background: linear-gradient(to left, #8942a8, #ce19df);
    color: white;
    transition: .5s ease;
}

.btn-animation a {
    text-decoration-style: none;
    color: white;
    transition: .5s ease;
}

.btn-animation a:hover {
    transition: .5s ease;
}